
@import 'reset';

* {
	box-sizing: border-box;
}

body {
	background: #E5F4FA;
}
.wrapper{
	margin: 30px;
	.small-ekg-svg{
			width: 100%;
			margin-top: 10%;
			margin-bottom: 10%;
		}
	header{
		.healthcare-banner{
			width: 40%;
			display: block;
			margin: 0 auto;
			margin-top: 2%;
		}

		p{
			color: #042A2B;
			text-align: center;
			font-size: 4vw;
			margin-top: 1%;
			line-height: 100%;
			font-family: 'ubuntu';
