	}
	.hospital{
		display: flex;
		flex-direction: row;
		position: relative;
		.hospital-svg{
			width: 30%;
			display: block;
			margin: 0 auto;
			margin-top: 2%;
		}
		.donkey-svg{
			width: 10%;
			position: absolute;
			left: 10%;
			bottom: 0;
		}
		.elephant-svg{
			width: 10%;
			position: absolute;
			right: 10%;
			bottom: 0;
		}
	}

	.party-lines{
		p{
			color: #042A2B;
			text-align: center;
			font-size: 4vw;
			margin-top: 3%;
			font-family: 'ubuntu';
		} 
	}

	.ekg-animation{
		position: relative;
		.ekg-box{
			height: 105px;
			width: 100%;
			background-color: white;
			position: absolute;
			top: 75px;
			transition: all 3s linear;
			margin-top: -25px;

			&:hover{
				transform: translateX(1000px);
			}
		} 

		// .small-ekg-svg{
		// 	width: 100%;
		// 	margin-top: 5%;
		// }
	}


	.the-numbers{
		display: flex;
		flex-direction: row;
		justify-content: center;

		.pill-row{
